'use client';
import React, { useState } from 'react';
import { StatCard } from '../../components/StatCard';
import { ChartCard } from '../../components/ChartCard';
import { API_BASE, DEV_TOKEN } from '../../lib/env';

export default function NetworkPage() {
  const [address, setAddress] = useState('');
  const [resp, setResp] = useState<string | null>(null);

  async function call(path: string, body: any) {
    const res = await fetch(`${API_BASE}/api/network/${path}`, {
      method: 'POST',
      headers: { 'content-type': 'application/json', authorization: `Bearer ${DEV_TOKEN}` },
      body: JSON.stringify(body),
    }).then((r) => r.json());
    setResp(JSON.stringify(res));
  }

  return (
    <div className="space-y-6">
      <h3 className="text-lg font-semibold">Сеть</h3>
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <StatCard title="Peers" value="—" />
        <StatCard title="Validators" value="—" />
        <StatCard title="Block time" value="—" />
        <StatCard title="Finality lag" value="—" />
      </div>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <ChartCard title="Block time (mock)" />
        <ChartCard title="Peer count (mock)" />
      </div>
      <div className="grid md:grid-cols-2 gap-4">
        <form onSubmit={(e) => { e.preventDefault(); call('permissioning/addAccount', { address }); }} className="border border-gray-800 rounded p-3 bg-gray-900">
          <div className="font-medium mb-2">Permissioning: Add Account</div>
          <div className="flex gap-2">
            <input className="border border-gray-700 rounded p-2 bg-gray-900 flex-1" placeholder="0x..." value={address} onChange={(e) => setAddress(e.target.value)} />
            <button className="px-3 py-2 rounded bg-green-600 text-white">Add</button>
          </div>
        </form>
        <form onSubmit={(e) => { e.preventDefault(); call('permissioning/removeAccount', { address }); }} className="border border-gray-800 rounded p-3 bg-gray-900">
          <div className="font-medium mb-2">Permissioning: Remove Account</div>
          <div className="flex gap-2">
            <input className="border border-gray-700 rounded p-2 bg-gray-900 flex-1" placeholder="0x..." value={address} onChange={(e) => setAddress(e.target.value)} />
            <button className="px-3 py-2 rounded bg-red-600 text-white">Remove</button>
          </div>
        </form>
      </div>
      {resp && <pre className="text-xs bg-gray-900 border border-gray-800 rounded p-3 whitespace-pre-wrap">{resp}</pre>}
    </div>
  );
}

