import React from 'react';

export function ChartCard({ title }: { title: string }) {
  const bars = [8, 14, 6, 10, 16, 12, 9, 13, 7, 11];
  return (
    <div className="p-4 rounded border border-gray-800 bg-gray-900">
      <div className="text-sm text-gray-400 mb-2">{title}</div>
      <div className="h-24 flex items-end gap-1">
        {bars.map((h, i) => (
          <div key={i} className="bg-blue-500/60" style={{ width: '8%', height: `${h * 6}px` }} />
        ))}
      </div>
    </div>
  );
}


